import React, { useState, useEffect } from 'react'
import DashboardLayout from '../../components/dashboard/DashboardLayout'
import StatCard from '../../components/dashboard/StatCard'
import ChartPlaceholder from '../../components/dashboard/ChartPlaceholder'
import ActivityFeed from '../../components/dashboard/ActivityFeed'
import { dashboardService } from '../../services/dashboardService'
import { useAppSelector } from '../../hooks/hooks'

interface DashboardStats {
  totalUsers: number
  activeUsers: number
  newUsersToday: number
  systemStatus: string
}

interface Activity {
  id: string
  type: 'create' | 'update' | 'delete' | 'login' | 'notification'
  title: string
  description: string
  time: string
  user?: string
}

const AdminDashboardPage: React.FC = () => {
  const [stats, setStats] = useState<DashboardStats>({
    totalUsers: 0,
    activeUsers: 0,
    newUsersToday: 0,
    systemStatus: 'loading'
  })
  const [activities, setActivities] = useState<Activity[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')
  const currentUser = useAppSelector(state => state.auth.user)

  useEffect(() => {
    loadDashboardData()
  }, [])

  const loadDashboardData = async () => {
    try {
      setLoading(true)
      setError('')

      // Load dashboard statistics and activities
      const [statsData, activitiesData] = await Promise.all([
        dashboardService.getStatistics(),
        dashboardService.getRecentActivities(10)
      ])

      setStats(statsData)
      // Transform activities to match the expected type
      const transformedActivities: Activity[] = activitiesData.map((activity: any) => ({
        id: activity.id,
        type: activity.type as 'create' | 'update' | 'delete' | 'login' | 'notification',
        title: activity.title,
        description: activity.description,
        time: activity.time,
        user: activity.user
      }))
      setActivities(transformedActivities)
    } catch (err: any) {
      console.error('Failed to load admin dashboard data:', err)
      setError(err.message || 'Failed to load dashboard data')
      
      // Set fallback data
      setStats({
        totalUsers: 0,
        activeUsers: 0,
        newUsersToday: 0,
        systemStatus: 'error'
      })
      setActivities([])
    } finally {
      setLoading(false)
    }
  }

  if (loading) {
    return (
      <DashboardLayout>
        <div className="min-h-screen flex items-center justify-center">
          <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-indigo-600"></div>
        </div>
      </DashboardLayout>
    )
  }

  if (error) {
    return (
      <DashboardLayout>
        <div className="min-h-screen flex items-center justify-center">
          <div className="text-center">
            <h2 className="text-2xl font-bold text-gray-900 mb-4">Error Loading Dashboard</h2>
            <p className="text-gray-600 mb-4">{error}</p>
            <button
              onClick={loadDashboardData}
              className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700"
            >
              Retry
            </button>
          </div>
        </div>
      </DashboardLayout>
    )
  }

  return (
    <DashboardLayout>
      <div className="space-y-6">
        {/* Page Header */}
        <div className="md:flex md:items-center md:justify-between">
          <div className="flex-1 min-w-0">
            <h2 className="text-2xl font-bold leading-7 text-gray-900 sm:text-3xl sm:truncate">
              Admin Dashboard
            </h2>
            <p className="mt-1 text-sm text-gray-500">
              Welcome back, {currentUser?.firstName || 'Admin'}! Here's your system overview.
            </p>
          </div>
          <div className="mt-4 flex md:mt-0 md:ml-4">
            <button
              type="button"
              className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
            >
              Export Report
            </button>
            <button
              type="button"
              className="ml-3 inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
            >
              System Settings
            </button>
          </div>
        </div>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
          <StatCard
            title="Total Users"
            value={stats.totalUsers.toString()}
            subtitle="Registered users"
            trend={{ value: stats.newUsersToday, isPositive: true }}
            color="blue"
            icon={
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197m13.5-9a2.25 2.25 0 11-4.5 0 2.25 2.25 0 014.5 0z" />
              </svg>
            }
          />
          <StatCard
            title="Active Users"
            value={stats.activeUsers.toString()}
            subtitle="Currently online"
            trend={{ value: 5, isPositive: true }}
            color="green"
            icon={
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            }
          />
          <StatCard
            title="New Users Today"
            value={stats.newUsersToday.toString()}
            subtitle="Registrations"
            trend={{ value: 12, isPositive: true }}
            color="yellow"
            icon={
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M18 9v3m0 0v3m0-3h3m-3 0h-3m-2-5a4 4 0 11-8 0 4 4 0 018 0zM3 20a6 6 0 0112 0v1H3v-1z" />
              </svg>
            }
          />
          <StatCard
            title="System Status"
            value={stats.systemStatus}
            subtitle="All systems operational"
            trend={{ value: 0, isPositive: true }}
            color="green"
            icon={
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m5.618-4.016A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z" />
              </svg>
            }
          />
        </div>

        {/* Charts and Activity Feed */}
        <div className="grid grid-cols-1 gap-6 lg:grid-cols-2">
          <ChartPlaceholder
            title="User Growth"
            subtitle="New user registrations over time"
            type="line"
            height="h-80"
          />
          <ActivityFeed activities={activities} />
        </div>

        <div className="grid grid-cols-1 gap-6">
          <ChartPlaceholder
            title="System Performance"
            subtitle="Server response times and uptime"
            type="area"
            height="h-64"
          />
        </div>
      </div>
    </DashboardLayout>
  )
}

export default AdminDashboardPage
