import { useEffect } from 'react'
import { useAppDispatch } from './hooks'
import { setUser } from '../state/slices/slice'
import { authService } from '../services/authService'
import { User as StateUser } from '../state/slices/slice'

const useAuthCheck = () => {
  const dispatch = useAppDispatch()

  useEffect(() => {
    const checkAuth = async () => {
      try {
        if (authService.isAuthenticated()) {
          const user = authService.getCurrentUser()
          const token = authService.getToken()
          
          if (user && token) {
            // Convert ApiUser to StateUser format (with Date objects)
            const stateUser: StateUser = {
              ...user,
              createdAt: new Date(user.createdAt),
              updatedAt: new Date(user.updatedAt)
            }
            
            dispatch(setUser({ user: stateUser, token }))
          }
        }
      } catch (error) {
        console.error('Auth check failed:', error)
        // Clear invalid auth data
        authService.clearAuthData()
      }
    }

    checkAuth()
  }, [dispatch])
}

export default useAuthCheck 